import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { HttpParams } from '@angular/common/http';
import { Observable, throwError, BehaviorSubject } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { Cookie } from 'ng2-cookies/ng2-cookies';

@Injectable({
  providedIn: 'root',
})
export class HomeService {
  // Url
  public cityUrl = '/city/get-city-list';
  public categoryListsUrl = '/category/get-category-list';
  public languageUrl = '/language/get-language-list';
  public dealsUrl = '/users/user-home';
  public userRegistrationUrl = '/users/user-registration';
  public userLoginUrl = '/users/user-login-password';
  public subCategoryListUrl = '/subcategory/get-subcategory-list';
  public userUpdateUrl = '/users/user-update-profile';
  public changePasswordUrl = '/users/change-password';
  public forgetPasswordUrl = '/users/user-forgot-password';
  public resetPasswordUrl = '/users/reset-password';
  public getPostUrl = '/posts/get-posts';

  // Behavior Subject
  public userName: string;
  count: BehaviorSubject<string>;

  public cityID: number;
  updatedID: BehaviorSubject<number>;

  public subCate: number;
  updateSubCat: BehaviorSubject<number>;

  public jobSubCate: number;
  updateJobSubCat: BehaviorSubject<number>;

  public show = false;
  isShow: BehaviorSubject<boolean>;
  // currentNameSubject = new BehaviorSubject();

  public index: number;
  i: BehaviorSubject<number>;

  constructor(public http: HttpClient) {
    this.isShow = new BehaviorSubject(this.show);
    this.count = new BehaviorSubject(this.userName);
    this.i = new BehaviorSubject(this.index);
    this.updatedID = new BehaviorSubject(this.cityID);
    this.updateSubCat = new BehaviorSubject(this.subCate);
    this.updateJobSubCat = new BehaviorSubject(this.jobSubCate);
  }

  httpOptions = {
    headers: new HttpHeaders({}),
  };

  nextCount() {
    this.count.next(Cookie.get('userName'));
  }
  nextCountBoolean(value) {
    this.isShow.next(value);
  }
  nextIndex(i: number) {
    this.i.next(i);
  }
  nextCityID(cityId: number){
    this.updatedID.next(cityId);
  }
  nextSubCatID(subCat: number){
    this.updateSubCat.next(subCat);
  }
  nextJobSubCatID(jobSubCat: number){
    this.updateJobSubCat.next(jobSubCat);
  }

  public setInfoInLocalStorage = (data) => {
    localStorage.setItem('userInfo', JSON.stringify(data));
  }
  public setPostInfoInLocalStorage = (data) => {
    localStorage.setItem('postDeatils', JSON.stringify(data));
  }

  public getUserInfoFromLocalStorage = () => {
    return JSON.parse(localStorage.getItem('userInfo'));
  }
  public getPostInfoFromLocalStorage = () => {
    return JSON.parse(localStorage.getItem('postDeatils'));
  }

  // getting category-list
  getCategoryList(): Observable<any> {
    const form = new FormData();
    const x =
      '[{"languageID": "1","searchWord": "","languageID": "1" ,"apiType": "Android","apiVersion": "1.0"}]';
    form.append('json', x);
    return this.http
      .post<any>(this.categoryListsUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // getting sub category-list
  getSubCategoryList(categoryID): Observable<any> {
    const form = new FormData();
    const x =
      '[{ "loginuserID": "0","searchWord":"", "categoryID":"' +
      categoryID +
      '", "languageID": "1", "apiType": "Android", "apiVersion": "1.0"}]';
    form.append('json', x);
    return this.http
      .post<any>(this.subCategoryListUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // getting language list
  getLanguageList(): Observable<any> {
    const form = new FormData();
    const x = '[{"loginuserID": "0","apiType": "Android","apiVersion": "1.0"}]';
    form.append('json', x);
    return this.http
      .post<any>(this.languageUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // getting deals
  getDeal(): Observable<any> {
    const form = new FormData();
    const x =
      '[{"loginuserID": "30","languageID": "1","page": "0","pagesize": "10","cityID":"1", "apiType": "Android", "apiVersion": "1.0"}]';
    form.append('json', x);
    return this.http
      .post<any>(this.dealsUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // creating user
  userRegistration(data): Observable<any> {
    const form = new FormData();
    const user =
      '[{"apiType": "Android","apiVersion": "1.0","languageID": "1","userFirstName": "' +
      data.userFirstName +
      '","userLastName": "' +
      data.userLastName +
      '","userEmail": "' +
      data.userEmail +
      '","userCountryCode": "' +
      data.userCountryCode +
      '","userMobile": "' +
      data.userMobile +
      '","userPassword":"' +
      data.userPassword +
      '","userDeviceType": "Android","userProfilePicture":"a.png","userFBID":"","userGoogleID":"0","userDeviceID":"100" }]';
    form.append('json', user);
    return this.http
      .post<any>(this.userRegistrationUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // userLogin
  userLogin(data): Observable<any> {
    const form = new FormData();
    const user =
      '[{"userPassword": "' +
      data.password +
      '", "userMobile": "' +
      data.emailOrMobileNumber +
      '","languageID": "1", "userDeviceID": "token", "apiType": "Android", "apiVersion": "1.0" }]';
    form.append('json', user);
    return this.http
      .post<any>(this.userLoginUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // getting city
  getCity(stateID): Observable<any> {
    const form = new FormData();
    const city =
      '[{ "loginuserID": "0",  "languageID": "1", "countryID":"1", "stateID":"' +
      stateID +
      '",  "apiType": "Android", "page" : "0", "pagesize": "10", "apiVersion": "1.0"}]';
    form.append('json', city);
    return this.http
      .post<any>(this.cityUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }
  // userUpdate
  userUpdate(userUpdate): Observable<any> {
    const form = new FormData();
    const user =
      '[{"apiType": "Android","apiVersion": "1.0", "languageID": "1", "loginuserID":"' +
      userUpdate.userID +
      '", "userFirstName": "' +
      userUpdate.firstName +
      '", "userLastName": "' +
      userUpdate.lastName +
      '",	"userEmail": "' +
      userUpdate.email +
      '", "userMobile": "' +
      userUpdate.mobileNumber +
      '", "userDeviceType": "Android", "userProfilePicture":"a.png",	"userDeviceID":"100" }]';
    form.append('json', user);
    return this.http
      .post<any>(this.userUpdateUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }
  // change password
  changePassword(passwordData): Observable<any> {
    const form = new FormData();
    const user =
      '[{"loginuserID": "' +
      passwordData.userID +
      '","userCurrentPassword": "' +
      passwordData.currentPassword +
      '","languageID": "1","userNewPassword": "' +
      passwordData.newPassword +
      '","apiType": "Android", "apiVersion": "1.0"}]';
    form.append('json', user);
    return this.http
      .post<any>(this.changePasswordUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // get post along with subcateId and cityId
  viewPost(getPostData): Observable<any> {
    const form = new FormData();
    const getPost =
      '[{"languageID": 1, "loginuserID": "' +
      getPostData.userID +
      '", "categoryID": "' +
      getPostData.categoryID +
      '", "searchWord": "", "categorytypeID": "0", "subcategoryID": "' +
      getPostData.subcategoryID +
      '", "postType": "Seller", "todaysDeal": "No", "cityID": "' +
      getPostData.cityID +
      '", "page": "0", "pagesize": "1000", "apiType": "iphone", "apiVersion": "1.0", "educationID": "", "otherlocation": "",  "postotherYearExp": "", "postotherArea": "", "postotherLocation": "","experianceID": "", "postLevel": "", "postLatitude": 23.035817, "postLongitude": 72.518362 }]';
    form.append('json', getPost);
    return this.http
      .post<any>(this.getPostUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }
  // forget password
  forgetPassword(newPassword): Observable<any> {
    const form = new FormData();
    const user =
      '[{"userEmail": "' +
      newPassword.email +
      '","userCountryCode": "91","languageID": "1","userMobile": "' +
      newPassword.mobileNumber +
      '","apiType": "Android","apiVersion": "1.0"}]';
    form.append('json', user);
    return this.http
      .post<any>(this.forgetPasswordUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }

  // reset password
  resetPassword(password): Observable<any> {
    const form = new FormData();
    const user =
      '[{"loginuserID": "' +
      password.userID +
      '","languageID": "1", "userNewPassword":"' +
      password.newPassword +
      '","apiType": "Android", "apiVersion": "1.0"}]';
    form.append('json', user);
    return this.http
      .post<any>(this.resetPasswordUrl, form, this.httpOptions)
      .pipe(retry(1), catchError(this.handleError));
  }
  // ErrorHandling
  handleError(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get Client Side Error
      errorMessage = error.error.messages;
    } else {
      // Get Server-Side Error
      errorMessage = `Error Code : ${error.status}\nMessage : ${error.messsage}`;
    }
    window.alert(errorMessage);
    return throwError(errorMessage);
  }
}
