import { Component, EventEmitter, Output, OnInit } from '@angular/core';
import { HomeService } from '../home.service';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { UpdateUserDataComponent } from '../update-user-data/update-user-data.component';
import {
  style,
  trigger,
  transition,
  animate,
  keyframes,
} from '@angular/animations';
import { Cookie } from 'ng2-cookies/ng2-cookies';
import { ToastrManager } from 'ng6-toastr-notifications';
import { NgForm } from '@angular/forms';
import { PasswordUpdate } from '../navbar/userInterface.component';

@Component({
  selector: 'app-my-account',
  templateUrl: './my-account.component.html',
  styleUrls: ['./my-account.component.scss'],
  animations: [
    trigger('moveInLeft', [
      transition('void=> *', [
        style({ transform: 'translateX(300px)' }),
        animate(
          200,
          keyframes([
            style({ transform: 'translateX(300px)' }),
            style({ transform: 'translateX(0)' }),
          ])
        ),
      ]),

      transition('*=>void', [
        style({ transform: 'translateX(0px)' }),
        animate(
          100,
          keyframes([
            style({ transform: 'translateX(0px)' }),
            style({ transform: 'translateX(300px)' }),
          ])
        ),
      ]),
    ]),
  ],
})
export class MyAccountComponent implements OnInit {
  public userInfo = [];
  public oldPassword: string;
  public newPassword: string;
  public reEnterNewPassword: string;
  public userName: string;
  public languageList = [];
  dog: any;

  // event emmitter
  // @Output() valueChange = new EventEmitter<string>();

  constructor(
    public homeService: HomeService,
    private dialog: MatDialog,
    public toastr: ToastrManager
  ) {}

  ngOnInit(): void {
    this.getLanguageList();
    setTimeout(() => {
      this.makeItFalse();
    });
    this.homeService.count.subscribe((c) => {
      this.userName = c;
      console.log('behavioral object', this.userName);
    });
    this.userInfo = this.homeService.getUserInfoFromLocalStorage();
  }
  nextCount() {
    this.homeService.nextCount();
  }
  makeItFalse() {
    const isTrue = false;
    this.homeService.nextCountBoolean(isTrue);
  }

  public passwordUpdate = (form: NgForm) => {
    if (!this.oldPassword) {
      this.toastr.warningToastr('Enter First Current Password.', 'Alert!');
    } else if (!this.newPassword) {
      this.toastr.warningToastr('Enter New Password', 'Alert!');
    } else if (!this.reEnterNewPassword) {
      this.toastr.warningToastr('Enter New Password', 'Alert!');
    } else if (this.newPassword !== this.reEnterNewPassword) {
      this.toastr.warningToastr('Enter Same password', 'Alert!');
    } else {
      const passwordData: PasswordUpdate = {
        userID: this.userInfo[0].userID,
        currentPassword: this.oldPassword,
        newPassword: this.newPassword,
      };
      this.homeService.changePassword(passwordData).subscribe(
        (response) => {
          if (response[0].status === 'true') {
            console.log('password updated successfully');
            this.toastr.successToastr(
              'Password Updated Successfully',
              'Success'
            );
            setTimeout(() => {
              form.resetForm();
            });
          } else {
            this.toastr.errorToastr(response[0].message);
          }
        },
        (error) => {
          this.toastr.errorToastr('Some error occured' + error);
        }
      );
    }
  }

  getLanguageList: any = () => {
    return this.homeService.getLanguageList().subscribe(
      (response) => {
        this.languageList = response[0].data;
        console.log(response);
        console.log(this.languageList);
      },
      // tslint:disable-next-line:no-shadowed-variable
      (error) => {
        this.toastr.errorToastr('Some error occured' + error);
      }
    );
  } // end of getLanguageList


  public openModal() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.data = {};
    const dialogRef = this.dialog.open(UpdateUserDataComponent, {
      height: '420px',
      width: '600px',
      data: this.userInfo[0],
    });
    dialogRef.afterClosed().subscribe((result) => {
      setTimeout(() => {
        console.log('done!');
        this.nextCount();
        // this.valueChange.emit('hello to parent');
        this.ngOnInit();
      }, 1000);
      console.log(' Dialog was closed');
    });
  }
}
